.catalog-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin-top: 60px;
}

.catalog {
  flex: 1;
  padding: 20px 20px;
  background-color: #f9f9f9;
}

.catalog h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  color: #333;
}

.product-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-top: 20px;
}

.pagination button {
  padding: 8px 16px;
  font-size: 16px;
  cursor: pointer;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  border-radius: 4px;
  transition: color 0.3s ease;
}

.pagination button:hover {
  color: rgb(179, 21, 179);
}

.pagination button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  color: #333;
}

.filters {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.filters select {
  text-align: center;
  margin-left: 8px;
  padding: 8px 8px;
  font-size: 14px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

@media (max-width: 768px) {
  .filters {
    text-align: center;
  }

  .filters select {
    margin: 8px 0px 0px 0px;
    width: 100%;
  }
}